<template> 
  <div class="app-container" style="width: 80%;margin: auto">
    <el-card class="operate-container" shadow="never">
      <i class="el-icon-tickets"></i>
      <span>订单信息</span>
      <span v-show="isSaved" style="color: red">（已保存）</span>
      <el-button size="mini" type="danger" class="btn-add" @click="handleResetAdd()" style="margin-left: 20px">重置</el-button>
      <el-button size="mini" class="btn-add" :loading="updateLoading" :disabled="updateLoading" @click="handleAddOrder('orderInfoForm')" style="margin-left: 20px">保存</el-button>
      <el-button size="mini" class="btn-add" :loading="updateLoading" :disabled="updateLoading" @click="handlePrintDialog('orderInfoForm')" style="margin-left: 20px">打印</el-button>
    </el-card>
    <div class="table-container" >

      <el-card shadow="never" style="margin-top: 15px">
        <div slot="header">
          <svg-icon icon-class="marker" style="color: #606266"></svg-icon>
          <span class="font-small">基本信息</span>
        </div>
        <el-form :model="order" :rules="rules" ref="orderInfoForm"  label-width="20%" style="width: 60%;" size="small">
          <el-form-item label="订单号：" prop="orderSn">
            <el-input v-model="order.orderSn"></el-input>
          </el-form-item>
          <el-form-item label="购买方：" prop="customerName">
            <el-select
              v-model="order.customerName"
              filterable
              allow-create
              default-first-option
              placeholder="请选择购买方"
              @change="handleChangeCustomerName">
              <el-option
                v-for="item in customerOptions"
                :key="item.key"
                :label="item.label"
                :value="item.value"
                >
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="日期：" prop="orderDate">
            <el-date-picker
              v-model="order.orderDate"
              type="date"
              placeholder="选择日期">
            </el-date-picker>
          </el-form-item>
          <el-form-item label="备注：" prop="orderOther">
            <el-input v-model="order.orderOther"
                      type="textarea"
                      :rows="3"></el-input>
          </el-form-item>
        </el-form>
      </el-card>
      <el-card class="" shadow="never" style="margin-top: 15px">
        <div slot="header" >
          <svg-icon icon-class="marker" style="color: #606266"></svg-icon>
          <span class="font-small">货品信息</span>
          <el-cascader style="margin-left: 20px;width: 30%"
            ref="cascaderSelect"
            v-model="stockSelect"
            filterable
            :options="stockOptions"
            :props="{ expandTrigger: 'hover' }"
            prop="stock"
            @change="handleChangeStock()">
            <template slot-scope="{ node, data }">
              <span>{{ data.label }}</span>
              <span v-if="node.isLeaf"> (剩余：{{ data.num + data.unit}}) </span>
            </template>
          </el-cascader>
          <el-input-number v-model="stockSelectInfo.num" style="margin-left: 20px;display: inline-block;width: 20%"></el-input-number>
          <el-button size="mini" type="primary" :disabled="stockSelectInfo.firm == null || stockSelectInfo.num == 0" class="btn-add" @click="addGood()" style="margin-left: 20px">添加</el-button>
        </div>
        <el-table
          ref="orderItemTable"
          :data="order.goodList"
          style="width: 100%;margin-top: 20px" border>
          <el-table-column label="供货商" width="120" align="center">
            <template slot-scope="scope">{{scope.row.firm}}</template>
          </el-table-column>
          <el-table-column label="货品名称" align="center">
            <template slot-scope="scope">{{scope.row.stock}}</template>
          </el-table-column>
          <el-table-column label="规格型号" align="center">
            <template slot-scope="scope">{{scope.row.model}}</template>
          </el-table-column>
          <el-table-column label="数量" align="center">
            <template slot-scope="scope">{{scope.row.num}}</template>
          </el-table-column>
          <el-table-column label="单价" align="center">
            <template slot-scope="scope">{{scope.row.price}}</template>
          </el-table-column>
          <el-table-column label="小计" align="center">
            <template slot-scope="scope">
              ￥{{scope.row.num*scope.row.price}}
            </template>
          </el-table-column>
          <el-table-column label="操作" align="center">
            <template slot-scope="scope">
              <el-button
                size="mini"
                type="danger"
                @click="handleDeleteGood(scope.$index, scope.row)">删除</el-button>
            </template>
          </el-table-column>
        </el-table>
        <div style="float: right;margin: 20px">
          合计：<span class="color-danger">￥{{order.totalAmount}}</span>
        </div>
      </el-card>
    </div>
    <el-dialog
      title="订单预览"
      :visible.sync="dialogVisible"
      :top = "'10vh'"
      width="60%">
      <el-card shadow="never" id="orderCard" ref="orderCard" style="">
        <div slot="header">
<!--          <svg-icon icon-class="marker" style="color: #606266"></svg-icon>-->
          <span class="font-small">订单</span>
        </div>
        <el-form :model="order" label-position="left" label-width="10%" style="width: 100%;" size="small">
          <el-form-item label="订单号：" prop="orderSn">
            <el-row> {{order.orderSn}} </el-row>
          </el-form-item>
          <el-form-item label="购买方：" prop="customerName">
            <el-row> {{order.customerName}} </el-row>
          </el-form-item>
          <el-form-item label="日期：" prop="orderDate">
            <el-row> {{order.orderDate | formatDateTime}} </el-row>
          </el-form-item>
          <el-form-item label="备注：" prop="orderOther">
            <el-row> {{order.orderOther}} </el-row>
          </el-form-item>
        </el-form>
        <el-table
          ref="orderItemTable"
          :data="order.goodList"
          style="width: 100%;margin-top: 20px">
          <el-table-column label="供货商" width="120" align="center">
            <template slot-scope="scope">{{scope.row.firm}}</template>
          </el-table-column>
          <el-table-column label="货品名称" align="center">
            <template slot-scope="scope">{{scope.row.stock}}</template>
          </el-table-column>
          <el-table-column label="规格型号" align="center">
            <template slot-scope="scope">{{scope.row.model}}</template>
          </el-table-column>
          <el-table-column label="数量" align="center">
            <template slot-scope="scope">{{scope.row.num}}</template>
          </el-table-column>
          <el-table-column label="单价" align="center">
            <template slot-scope="scope">{{scope.row.price}}</template>
          </el-table-column>
          <el-table-column label="小计" align="center">
            <template slot-scope="scope">
              ￥{{scope.row.num*scope.row.price}}
            </template>
          </el-table-column>
        </el-table>
        <div style="float: right;margin: 20px">
          合计：<span class="color-danger">￥{{order.totalAmount}}</span>
        </div>
      </el-card>
      <div style="text-align: center;margin-top: 20px">
        <el-button type="primary" size="medium" v-print="'#orderCard'" @click="handleAddOrder('orderInfoForm')">打印并保存</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
  import {fetchFirmList, fetchStockList, updateStockList} from "@/api/stock";
  import {addCustomer, fetchCustomerNameList, updateCustomer} from "@/api/customer";
  import {formatDate} from "@/utils/date";
  import {addOrder} from "@/api/order";
  export default {
    name: 'addOrder',
    data() {
      return {
        listLoading: false,
        updateLoading: false,
        rules: {
          orderSn:  [{required: true, message: '请输入', trigger: 'blur'}],
          customerName: [{required: true, message: '请输入', trigger: 'change'}],
          orderDate: [{required: true, message: '请输入', trigger: 'blur'}],
          orderOther: [{required: false, message: '请输入', trigger: 'blur'}],
        },
        order : {},
        defaultOrder:{
          orderSn: null,
          customerName: '',
          customerId: 0,
          totalAmount: 0,
          orderDate: null,
          orderOther: null,
          goodList: []
        },
        customerOptions: [],
        stockOptions: [],
        stockSelect: [],
        stockSelectInfo: {
          firm: null,
          stock: null,
          model: null,
          price: null,
          num: 0
        },
        dialogVisible: false,
        isSaved: false
      }
    },
    created() {
      this.order =  Object.assign({}, this.defaultOrder);
      this.getCustomerList();
      this.getStockList();
    },
    filters: {
      formatDateTime(time) {
        if (time == null || time === '') {
          return 'N/A';
        }
        let date = new Date(time);
        return formatDate(date, 'yyyy-MM-dd')
      }
    },
    methods: {
      handleResetAdd() {
        this.order =  Object.assign({}, this.defaultOrder);
        this.order.goodList = [];
        this.isSaved = false;
      },

      handleAddOrder(formName){
        this.$refs[formName].validate((valid) => {
          if (valid) {

              this.updateLoading = true;
              addOrder(this.order).then(response => {
                this.$message({
                  message: '保存成功',
                  type: 'success',
                  duration: 1000
                });
                // console.log("AddOrder");
                this.updateLoading = false;
                this.dialogVisible = false;
                this.isSaved = true;
              });

          }
        });
      },
      getCustomerList() {
        fetchCustomerNameList().then(response => {
          this.customerOptions = [];
          let customerList = response.data.list;
          // console.log(customerList)
          for (let i = 0; i < customerList.length; i++) {
            this.customerOptions.push({label: customerList[i].customerName, value: customerList[i].customerName, key: customerList[i].customerId});
          }
        });
      },
      handleChangeCustomerName(customerName){
        this.order.customerId = 0;
        this.customerOptions.forEach( customer => {
          if (customer.value == customerName) {
            this.order.customerId = customer.key;
            return;
          }
        });
      },
      handleChangeStock(){
        this.stockSelectInfo.firm = this.stockSelect[0];
        this.stockSelectInfo.stock = this.stockSelect[1];
        this.stockSelectInfo.model = this.stockSelect[2];
        this.stockSelectInfo.price = this.$refs['cascaderSelect'].getCheckedNodes()[0].data.price;
      },
      getStockList() {
        fetchStockList({}).then(response => {
          let stockList = response.data.list;
          let oneList = [];
          let twoList = [];
          let threeList = [];
          let firm = '';
          let stock = '';
          let model = '';
          if (stockList.length > 0){
            firm = stockList[0].firm;
            stock = stockList[0].stock;
            model = stockList[0].model;
          }
          stockList.forEach(stockItem => {
            if (stock != stockItem.stock){
              twoList.push({label: stock, value: stock, children: [...threeList]});
              threeList = [];
            }

            if (firm != stockItem.firm){
              oneList.push({label: firm, value: firm, children: [...twoList]});
              twoList = [];
            }

            threeList.push({label: stockItem.model, value: stockItem.model, num: stockItem.num, unit: stockItem.unit, price: stockItem.price});
            //记录本次，下次与本次进行比对判断是否生成二级菜单和三级菜单
            stock = stockItem.stock;
            firm = stockItem.firm;
            model = stockItem.model;
          });
          twoList.push({label: stock, value: stock, children: [...threeList]});
          oneList.push({label: firm, value: firm, children: [...twoList]});
          this.stockOptions = [...oneList];
        });
      },
      addGood(){
        this.order.goodList.push({firm:this.stockSelectInfo.firm, stock:this.stockSelectInfo.stock, model:this.stockSelectInfo.model,
          price:this.stockSelectInfo.price, num:parseInt(this.stockSelectInfo.num,10)});
        this.order.totalAmount += this.stockSelectInfo.price * this.stockSelectInfo.num;
      },
      handleDeleteGood(index, row) {
        // console.log(index, row);
        this.order.goodList.splice(index,1);
        this.order.totalAmount -=  row.price * row.num;
      },
      handlePrintDialog(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            this.dialogVisible = true;
          }
        });
      }
    }
  }
</script>
<style>
</style>



